<template>
  <div>
    <register-app
      :alertInfo="alertInfo"
      :isBarber="true"
      :progressLoader="loading"
      @register="register"
      @registerWithGoogle="registerWithGoogle"
    />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import Overlay from '@/components/globais/overlay/Overlay'
import RegisterApp from '@/components/auth/Register'
import BarberMixin from '@/mixins/BarberMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'Register',
  components: { Overlay, RegisterApp },
  mixins: [ BarberMixin, SnackbarMixin, NavigationMixin ],
  data: () => ({
    alertInfo: null,
    loading: false,
    token: null
  }),
  methods: {
    register (data) {
      data.token = this.token
      this.loading = true
      Axios.post('/auth/barber/register', data)
      .then(response => {
        this.loading = false
        if (response.data.codigo === 200) this.alertInfo = "Obrigado por ter se cadastrado!<br>Verifique sua caixa de e-mail e confirme o seu cadastro."
        else this.showSnackbarErro(response.data.corpo)
      })
      .catch (() => { 
        this.loading = false
        this.showSnackbarErro('No foi possvel realizar seu cadastro!') 
      })
    },
    registerWithGoogle (data) {
      this.$refs.overlay.showOverlay('Carregando...')
      Axios.post('/auth/barber/register-google', data)
      .then(response => {
        if (response.data.codigo === 200) {
          this.showSnackbar('Cadastro realizado com sucesso!')
          this.setBarber(response.data.corpo)
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('No foi possvel realizar seu cadastro!'))
    },
    setBarber (token) {
      this.storeBarber(token)
      this.goTo('admin')
    },
    setError (msg) {
      this.$refs.overlay.hideOverlayError(msg)
      this.showSnackbarErro(msg)
    }
  }
}
</script>